<template>
  <div class="cardCountDown">
    <div class="count-down" :class="openCountDown ? 'start-animation' : ''">
      <div class="current-num card1">
        {{ current - 1 }}
      </div>
      <div class="current-num card2" :style="style">{{ current - 1 }}</div>
      <div class="current-num card3" :style="style1">{{ current }}</div>
      <div class="current-num card4">{{ current }}</div>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from "vue";
export default {
  setup() {
    const current = ref(10);
    const style = reactive({
      transform: "",
      transition: "0s",
    });
    const style1 = reactive({
      transform: "",
      transition: "0s",
    });
    const openCountDown = ref(false);
    function countDown() {
      openCountDown.value = true;
      const time = setInterval(() => {
        if (current.value <= 1) {
          openCountDown.value = false;
          clearInterval(time);
        }
        current.value--;
      }, 1000);
    }

    return { current, style, style1, countDown, openCountDown };
  },
  mounted() {
    this.countDown();
  },
};
</script>
<style lang="scss" scoped>
.cardCountDown {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #1e273be9;

  .count-down {
    width: 80px;
    height: 100px;
    font-size: 50px;
    position: relative;

    .current-num {
      position: absolute;
      width: 100%;
      height: 50%;
      background-color: #fff;
      border: 1px solid #d0d0d0;
      left: 0;
      overflow: hidden;
      // transition: 0.5s;
      &.card1 {
        top: 0;
        line-height: 200%;
        border-bottom: 2px solid #d0d0d0;
      }
      &.card2 {
        top: 50%;
        line-height: 0;
        transform: rotateX(180deg);
        transform-origin: center top;
        backface-visibility: hidden;
        z-index: 10;
      }
      &.card3 {
        line-height: 200%;
        transform-origin: center bottom;
        border-bottom: 1px solid #8a8a8a;
      }
      &.card4 {
        top: 50%;
        line-height: 0;
      }
    }
    &.start-animation {
      .card2 {
        animation: rotateBox 1s backwards infinite;
      }
      .card3 {
        animation: rotateBox1 1s backwards infinite;
        // transform: scale(1.01) ;
      }
    }
    @keyframes rotateBox {
      100% {
        transform: rotateX(0deg);
      }
    }
    @keyframes rotateBox1 {
      100% {
        transform: rotateX(-180deg);
      }
    }
  }
}
</style>